import React, { Component } from 'react';
import logo from './logo.svg';
import './App.css';

class App extends Component {
  render() {
    return (
      <div className="App">
        <header className="App-header">
        <div id="background-parallax">
          <div className="background-colours">
          <div className="background"></div>
        </div>
        <div className="background-animation">
          <div id="stars"></div>
          <div id="stars2"></div>
          <div id="stars3"></div>
        </div>
        <div className="mid-box-background">
          <div className="title-wrapper">
            <h1 id="homepage-title" className="animated bounceInDown">I'm Nicholas Griffin</h1>
            <h2 id="homepage-subtitle" className="animated fadeIn">Web Developer, Blogger, and technology Enthusiast</h2>
          </div>
          <div className="buttons-wrapper"></div>
        </div>
        <div className="scroll-down">
          <div className="scroll-icon-wrapper">
          <div className="scroller"></div>
        </div>
      </div>
    </div>
  </header>
  <section className="App-content">
  <div className="ui container">

    <div>
          {this.props.children}
        </div>

    <h2>Send me a message</h2>

    <form method="POST" className="ui form" action="https://formspree.io/me@nicholasgriffin.co.uk">
    <div className="field">
    <label htmlFor="name">Name</label>
    <input type="text" name="name" required/>
    </div>
    <div className="field">
    <label htmlFor="email">Email</label>
    <input type="email" name="email" required/>
    </div>
    <div className="field">
    <label htmlFor="message">Your Message</label>
    <textarea name="message" rows="2" required></textarea>
  </div>

    <button className="ui button" type="submit">Submit</button>
  </form>
  
  </div>
        </section>
      </div>
    );
  }
}

export default App;



